<template>
    <div class="inputBox shadow">
        <input type="text" v-model="newTodoItem" placeholder="  ." v-on:keyup.enter="addTodo">
        <span class="addContainer" v-on:click="addTodo">
            <i class="addBtn fa fa-plus" aria-hidden="true"></i>
        </span>

        <modal v-if="showModal" v-on:close="showModal = false">
            <h2 slot="header"></h2>
            <slot slot="body">  .</slot>
            <span slot="footer" v-on:click="showModal = false">
                OK
            </span>
        </modal>
    </div>
</template>
<script>
import Modal from './common/Modal.vue'

export default {
    props: ['propsdata'],
    data() {
        return {
            newTodoItem: '',
            showModal: false,
        }
    },
    methods: {
        addTodo: function() {
            if (this.newTodoItem !== "") {
                var value = this.newTodoItem && this.newTodoItem.trim();
                this.$emit('addTodoEvent', value);
                this.clearInput();
            } else {
                this.showModal = !this.showModal;
            }
        },
        clearInput: function() {
            this.newTodoItem = '';
        }
    },
    components: {
        Modal: Modal,
    }
}
</script>
<style>
    input:fouss {
        outline: none;
    }
    .inputBox {
        background: white;
        height: 50px;
        line-height: 50px;
        border-radius: 5px;
    }
    .inputBox input {
        border-style: none;
        font-size: 0.9rem;
    }
    .addContainer {
        float: right;
        background: linear-gradient(to right, #6478FB, #8763FB);
        display: block;
        width: 3rem;
        border-radius: 0 5px 5px 0;
    }
    .addBtn {
        color: white;
        vertical-align: middle;
    }
</style>


